import React,{useLayoutEffect, useState, useRef,useEffect} from 'react'
import "./programming.css"
import {useCollectionData} from "react-firebase-hooks/firestore"
import {db,auth} from "../firebase-config"
import { collection } from "firebase/firestore"
import { query, where,updateDoc,doc } from "firebase/firestore";  
import Videol from '../components/Videol'
import Videot from '../components/Videot'
import "./Quiz.css"
import Swal from "sweetalert2";  

export default function Programming() {

    const userCollectionRef = collection(db,"users")
    const webdataCollectionRef = collection(db,"website-data")
    const queryuser = query(userCollectionRef,where("uid","==",auth.currentUser.uid));
    const [data] = useCollectionData(queryuser,{idField:'id'});
    const [webdata] = useCollectionData(webdataCollectionRef,{idField:'id'});
    let usewebdata = webdata && webdata.filter(m=>m.id ==="programming-videos");
    const fram = useRef(null);
    const videoslist = useRef(null);

    function useWindowSize() {
        //onresize code
        const [size, setSize] = useState([0, 0]);
        useLayoutEffect(() => {
            function updateSize() {
            setSize([window.innerWidth, window.innerHeight]);
            }
            window.addEventListener('resize', updateSize);
            updateSize();
            return () => window.removeEventListener('resize', updateSize);
        }, []);
        //my code
        if(fram.current != null ){
        var framwidth = fram.current.clientWidth;
        var framheight = (9*framwidth)/16;
        fram.current.style.height =`${framheight}px`;
        videoslist.current.style.height =`${framheight + 160}px`;
        }
        return size;
    }
    useWindowSize();

    const [src, setsrc] = useState("https://www.youtube.com/embed/Opny9y21OdE")
    const [title, settitle] = useState("الحلقة 1 فى لغة السي شارب | Introduction ")
    const [dis, setdis] = useState("فى المقدمة دى هنشرح بطريقة عامة الاكواد البتظهر لما نفتح المحررمميزات لغة السي شارب1- لغة سلسة وغير معقدة2- تستخدم فى العديد من مجالات البرمجة واهمها برمجة الالعاب وهو موضوع القناة__________________________لو انته عايز تتعلم برمجة الالعاب فابدأ من دلوقتيدروس السي شارب : https://www.youtube.com/watch?v=90LB2...دروس اليونتي : https://www.youtube.com/watch?v=UA1tF...___________________________معلومات عن القناة وصاحب القناةقناتنا بتهتم ببرمجة الالعاب من الصفر فلو انته مهتم ببرمجة الالعاب على محرك اليونتي Unity Engine يبقي انته فى المكان الصح القناة دى هتمسك ايدك وتدخلك مجال برمجة الالعاب من الصفر لحد ماتبقي متعلم كل الاساسيات وتقدر تعمل لعبتك الخاصة والجودة الحالية للقناة ليست جودة دائمة وانما بنتحسن مع الوقت انشاء الله قناتنا شرح برمجة العاب بلغة عربية وكمان بطريقة سهلة وسلسة القناة بتحتوى على دورة فى السي شارب  لان من الطبيعي قبل ماتدخل على برمجة الالعاب لازم تكون كويس فى اللغة دى فالقناة زى ماقلت انها بتعلمك برمجة الالعاب من الصفر من قبل مايبقي عندك أى فكرة عن البرمجة ايه اليميز قناتنا عن القنوات الاخرى : محتوى منتظم و3 فيديوهات فى الاسبوع على الاقل غير اننا مستمرين فى الشرح يعني مش هنشرح برمجة الالعاب فترة وبعدها القناة تقف معلومات عني (صاحب القناة) :اسمي معاذ سليمان طالب مصرى بكلية الهندسة الالكترونية و مبرمج العاب وقبل ماكون مبرمج العاب كنت مبرمج ويب لفترة طويلة بس انتقلت لبرمجة الالعاب لانها بتمثل تحدى ليا وانا بحب التحدي غير انى عندى شغف كبير للالعاب ممكن يوصل لحد الادمان عملت القناة دى علشان أشجع نفسي وأطور من نفسي يوم ورى يوم مش عاملها بهدف مادى عاملها بهدف مساعدة الناس انهم يدخلوا المجال ولانها جزىء من حلمي وشعارى فى الحياة (خير الناس أنفعهم للناس)______________________________#ِِAnimo #CS#برمجة_الالعاب______________________________Adventures by A Himitsu https://www.youtube.com/channel/UCgFwu-j5-xNJml2FtTrrB3A Creative Commons — Attribution 3.0 Unported— CC BY 3.0 http://creativecommons.org/licenses/by/3.0/ Music released by Argofox https://youtu.be/8BXNwnxaVQE Music provided by Audio Library https://youtu.be/MkNeIUgNPQ8 ")
    const [currenttag, setcurrenttag] = useState(0);

    const clickhandle=(tag)=>{
        setsrc(usewebdata && usewebdata[0].videos[tag].src)
        settitle(usewebdata && usewebdata[0].videos[tag].title)
        setdis(usewebdata && usewebdata[0].videos[tag].dis)
        setcurrenttag(tag);
        for (let i = 0; i < videoslist.current.childNodes.length; i++) {
            videoslist.current.childNodes[i].classList.remove("current");
        }
        videoslist.current.childNodes[currenttag].classList.add("current");
    }
    useEffect(() => {
        if(videoslist.current.childNodes[currenttag]!==undefined)
        {
            for (let i = 0; i < videoslist.current.childNodes.length; i++) {
                videoslist.current.childNodes[i].classList.remove("current");
            }
            videoslist.current.childNodes[currenttag].classList.add("current");
        }
    }, [currenttag]);
    


    //quiz code
    let usequizzes = webdata && webdata.filter(m=>m.id ==="programming-quizzes");
    //console.log(usequizzes && usequizzes[0][0])
    const quizblock = useRef(null);
    const submitbtn = useRef(null);
    const nextbtn = useRef(null);
    const answers = useRef(null);
    const back = useRef(null);

    const [q, setq] = useState("");
    const [a1, seta1] = useState("");
    const [a2, seta2] = useState("");
    const [a3, seta3] = useState("");
    const [a4, seta4] = useState("");
    const [currentq,setcurrentq]=useState(0);

    const [right, setright] = useState("");
    const [lengthq, setlengthq] = useState(usequizzes && usequizzes[0][currenttag].length);
    const [score, setscore] = useState(0);
    
    //console.log(usequizzes && usequizzes[0][currenttag].length);

    const [quizcheck, setquizcheck] = useState(0);
    const quizclickhandle=async(tag)=>{
        if(data[0].programmingq[tag] ===undefined)
        {
        //console.log(usequizzes && usequizzes[0][tag]);
        setcurrenttag(tag);
        setright(usequizzes && usequizzes[0][currenttag][currentq].correcta);
        setquizcheck(quizcheck+1);
        //console.log(usequizzes && usequizzes[0][tag][currentq],usequizzes && usequizzes[0][tag].length-1);
        setlengthq(usequizzes && usequizzes[0][tag].length);
        quizblock.current.style.display="block";
        back.current.style.display="block";
        // const userDoc = doc(db,"users",data && data[0].id);
        // let nexttag = tag+1;
        // if(data[0].programming[nexttag] ===undefined)
        // {
        //     const newFields = {programming:{
        //         ...data[0].programming,
        //         [tag]:true,
        //         [nexttag]:false
        //     }};
        //     await updateDoc(userDoc,newFields);
        // }
        // for (let i = 0; i < videoslist.current.childNodes.length; i++) {
        //     videoslist.current.childNodes[i].classList.remove("current");
        // }
        // videoslist.current.childNodes[current].classList.add("current");
        }
        else if(data && data[0].programmingq[tag]< new Date().getTime())
        {
            setcurrenttag(tag);
        setright(usequizzes && usequizzes[0][currenttag][currentq].correcta);
        setquizcheck(quizcheck+1);
        //console.log(usequizzes && usequizzes[0][tag][currentq],usequizzes && usequizzes[0][tag].length-1);
        setlengthq(usequizzes && usequizzes[0][tag].length);
        quizblock.current.style.display="block";
        back.current.style.display="block";
        }
        else
        {
            Swal.fire({
                icon: 'error',
                title: 'درجتك اقل من النصف',
                text: `حاول مجددا بعد 12 ساعة`
            })
        }
        
    }

    useEffect(() => {
        addqdata(usequizzes && usequizzes[0][currenttag][currentq],usequizzes && usequizzes[0][currenttag].length-1);
    }, [quizcheck,currenttag]);
    

    const addqdata = (obj,count)=>{
        if(obj !== undefined){
        setq(obj.title);
        seta1(obj.a1);
        seta2(obj.a2);
        seta3(obj.a3);
        seta4(obj.a4);
        }
    }

    const [subcheck, setsubcheck] = useState(0);

    const submitClick=()=>{
        setright(usequizzes && usequizzes[0][currenttag][currentq].correcta);
        setright(usequizzes && usequizzes[0][currenttag][currentq].correcta);
        setsubcheck(subcheck+1);
    }

    let righta;
    useEffect(() => {
        checkanswer(right);
    }, [right,subcheck]);

    const checkanswer=(rightanswer)=>{
        let checkeda;
        
        let is;

        for (let i = 0; i < answers.current.childNodes.length; i++) {
            if(answers.current.childNodes[i].childNodes[0].checked){
                checkeda =answers.current.childNodes[i];
            }
            if(answers.current.childNodes[i].childNodes[1].innerText === rightanswer)
            {
                righta = answers.current.childNodes[i];
            }
        }
        is = checkeda === righta;

        if(checkeda !== undefined){
            if(!is)
            {
                checkeda.style.backgroundColor="#ff3838";
                righta.style.backgroundColor="#32ff7e";
                righta.style.color="black";

            }
            else if(is)
            {
                righta.style.backgroundColor="#32ff7e";
                righta.style.color="black";
                setscore(score+1);
            }
            submitbtn.current.style.display="none";
            nextbtn.current.style.display="block";
        }

    }
    
    const nextClick=(rightanswer)=>{
        if(currentq<lengthq)
        {
            setcurrentq(currentq + 1);
        }
        else
        {
            TheEndOfQuiz();
            closequiz();
        }
    }

    useEffect(() => {
    let checkeda;
    let righta;

    for (let i = 0; i < answers.current.childNodes.length; i++) {
        if(answers.current.childNodes[i].childNodes[0].checked){
            checkeda =answers.current.childNodes[i];
        }
        if(answers.current.childNodes[i].style.backgroundColor === "rgb(50, 255, 126)"){
            righta =answers.current.childNodes[i];
        }
    }
    if(checkeda != undefined){
        checkeda.childNodes[0].checked = false;
        checkeda.style.backgroundColor="#2c2936";
        righta.style.backgroundColor="#2c2936";
        righta.style.color="white";
        submitbtn.current.style.display="block";
        nextbtn.current.style.display="none";
        if(currentq < lengthq){
            addqdata(usequizzes && usequizzes[0][currenttag][currentq],usequizzes && usequizzes[0][currenttag].length-1);
        }
        else{
            TheEndOfQuiz();
            closequiz();
        }
    }
    }, [currentq])

    const closequiz=()=>{
        setcurrentq(0);
        setscore(0);
        let checkeda;
        let righta;
        back.current.style.display="none";
        for (let i = 0; i < answers.current.childNodes.length; i++) {
            if(answers.current.childNodes[i].childNodes[0].checked){
                checkeda =answers.current.childNodes[i];
            }
            if(answers.current.childNodes[i].style.backgroundColor == "rgb(50, 255, 126)"){
                righta =answers.current.childNodes[i];
            }
        }
        if(checkeda !== undefined){
            checkeda.childNodes[0].checked = false;
            checkeda.style.backgroundColor="#2c2936";
            righta.style.backgroundColor="#2c2936";
            righta.style.color="white";
            submitbtn.current.style.display="block";
            nextbtn.current.style.display="none";
        }

        quizblock.current.style.display="none";
    }


    const TheEndOfQuiz=()=>{
        if(score/lengthq>=0.5)
        {
            //xp
            let xp= score*100;
            if(data[0].programmingxp[currenttag]<xp || data[0].programmingxp[currenttag] === undefined)
            {
                let newFields;
                const userDoc = doc(db,"users",(data[0].id));
                if((data[0].exp+xp)>=((data[0].level+1)*1000)){
                newFields = {exp:0,level: data[0].level+1};
                }else if((data[0].exp+xp)!==((data[0].level+1)*1000))
                {
                    if(data[0].programmingxp[currenttag] !== undefined){
                        newFields={exp:(data[0].exp+xp-data[0].programmingxp[currenttag])}
                    }
                    else{
                        newFields={exp:(data[0].exp+xp)}
                        
                    }
                }
        
                updateDoc(userDoc,newFields);
                //nextvideo
                const user2Doc = doc(db,"users",data && data[0].id);
                let nexttag = currenttag+1;
                const new2Fields = {programming:{
                    ...data[0].programming,
                    [currenttag]:true,
                    [nexttag]:false
                }};
                updateDoc(user2Doc,new2Fields);
                

                //save the xp

                const user4Doc = doc(db,"users",data && data[0].id);
                const new4Fields = {programmingxp:{
                    ...data[0].programmingxp,
                    [currenttag]:xp,
                }};
                updateDoc(user4Doc,new4Fields);
            }
            Swal.fire({
                icon: 'success',
                title: 'مبروك',
                text: `تستطيع الانتقال الي الدرس التالي ${score + "/" + lengthq} درجتك`
            })
        }
        else
        {
            Swal.fire({
                icon: 'error',
                title: 'درجتك اقل من النصف',
                text: `حاول مجددا بعد 12 ساعة`
            })

            const user3Doc = doc(db,"users",data && data[0].id);
            let date = new Date();
            const new3Fields = {programmingq:{
                ...data[0].programmingq,
                [currenttag]:date.setTime(date.getTime()+(12*60*60*1000))
            }};
            updateDoc(user3Doc,new3Fields);
            //let date = new Date();
            //date.setTime(date.getTime()+(12*60*60*1000))


            //new Date().getTime()

        }
    }

    const pra = useRef(null);
    const morebtn = useRef(null);
    const dots = useRef(null);
    const [ispra, setispra] = useState(false)
    const [pratext, setpratext] = useState("show more")

    const clickmorebtn=()=>{
        setispra(!ispra);
        if (ispra) {
            pra.current.style.height="max-content";
            setpratext("show less");
            dots.current.style.display="none";
        }
        else{
            pra.current.style.height="297px";
            setpratext("show more");
            dots.current.style.display="block";
        }
    }

    return (
        <div className="container">
            <div className="back" ref={back}></div>
        {/*
            -------------------the quiz---------------------
        */}
            <div className="Quiz" >
                <div className="quiz-app"ref={quizblock}>
                    <i className="fas fa-times" onClick={closequiz}></i>
                    <div className="quiz-info">
                        <div className="category">Category: <span>Programming</span></div>
                        <div className="count">Questions: <span>{lengthq}</span></div>
                    </div>
                    <div className="quiz-area">
                        <h2>{q}</h2>
                    </div>
                    <div className="answers-area" ref={answers}>
                        <div className="answer">
                            <input type="radio" id="a1" name='ques'/>
                            <label htmlFor="a1">{a1}</label>
                        </div>
                        <div className="answer">
                            <input type="radio" id="a2" name='ques'/>
                            <label htmlFor="a2">{a2}</label>
                        </div>
                        <div className="answer">
                            <input type="radio" id="a3" name='ques'/>
                            <label htmlFor="a3">{a3}</label>
                        </div>
                        <div className="answer">
                            <input type="radio" id="a4" name='ques'/>
                            <label htmlFor="a4">{a4}</label>
                        </div>
                    </div>
                    <button className="submit-button" ref={submitbtn} onClick={submitClick}>Submit Answer</button>
                    <button className="next-button" ref={nextbtn} onClick={()=>nextClick(usequizzes && usequizzes[0][currenttag][currentq].correcta)}>Next Question</button>
                </div>
            </div>
            {/*
            -------------------the body---------------------
             */}
        <h1 className="title">{title}</h1>
            <div className="programming">
                <div className="videoside">
                <iframe ref={fram}  width="100%" src={src} title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                <h2>وصف الفيديو</h2>
                <p ref={pra} >{dis}</p>
                <div ref={dots} className="dots" >..............</div>
                <div className='morebtn' ref={morebtn} onClick={clickmorebtn}>{pratext}</div>
                </div>
                <div className="listside">
                    <h2>محتويات القسم</h2>
                    <div className="videosList" ref={videoslist}>

                    {
                        usewebdata && usewebdata[0].videos.map((item,i) => {
                            if(data && data[0].programming[i]===false){
                                return(
                                <Videol status="f" title={item.title} keyt={i} key={i} handleclick={clickhandle} quizclick={quizclickhandle}/>
                                )
                            }else if(data && data[0].programming[i] === true)
                            {
                                return <Videol status="t" title={item.title} keyt={i} key={i} handleclick={clickhandle} quizclick={quizclickhandle}/>
                            }else if(data && data[0].programming[i] === undefined)
                            {
                                return <Videot status="u" title={item.title} keyt={i} key={i}/>
                            }
                        })
                    }
                    </div>
                </div>
            </div>
        </div>
    )
}
