import './App.css';
import Login from './screens/login';
import {useAuthState} from "react-firebase-hooks/auth"
import {auth} from "./firebase-config"
import Home from './screens/Home';
import {BrowserRouter , Route ,Routes,Navigate  } from "react-router-dom";
import Header from './components/Header';
import Footer from './components/Footer';
import Programming from './screens/Programming';
import Unity from './screens/Unity';
import Discussion from './screens/Discussion';
import Codes from './screens/Codes';

function App() {
  const [user] = useAuthState(auth);

  if(user)
  {
    return(
    <BrowserRouter>
      <Header/>
      <Routes>
        <Route path={process.env.PUBLIC_URL + "/"} exact element={<Home/>} />
        <Route path={process.env.PUBLIC_URL + "/programming"} exact element={<Programming/>} />
        <Route path={process.env.PUBLIC_URL + "/unity"} exact element={<Unity/>} />
        <Route path={process.env.PUBLIC_URL + "/discussion"} exact element={<Discussion/>} />
        <Route path={process.env.PUBLIC_URL + "/addcode"} exact element={<Codes/>} />
      </Routes>
      <Footer />
    </BrowserRouter>
    )
  }
  else
  {
    return(
    <Login />
    )
  }
}

export default App;
